}

/*----------------
----- MIXINS -----
----------------*/

@mixin Button {
    margin: auto;
    font-family:'SpecialFont';
    color: $colorLightGrey;
    background-color: $colorDarkGrey;
    font-size: 25px;
    padding: 10px;
    border: none;
   
    // border-radius: 50rem;
    &:hover {
        color: $colorDarkGrey;
        background-color: $colorFirst;
        cursor: pointer;
    }
}

@mixin AddFieldButton {
    margin: auto;
    font-family:'SpecialFont';
    color: $colorLightGrey;
    font-size: 25px;
    padding: 10px;
    border: none;
    background-color: $colorDarkGrey;
    // border-radius: 50rem;
    &:hover {
        color: $colorLightGrey;
        background-color: $colorDarkGrey;
        cursor: pointer;
    }
}


@mixin ButtonSelected {
    // margin: auto;
    font-family:'SpecialFont';
    font-size: 25px;
    padding: 10px;
    border: none;
    color: $colorDarkGrey;
    background-color: $colorFirst;
    // border-radius: 50rem;
    &:hover {
        
        cursor: pointer;
    }
}

@mixin mainGalleryRatio1 {
    aspect-ratio: 2/1;
}

@mixin responsiveMarginLeft {
    margin-left: 10%;
}

/*-------------------
----- VARIABLES -----
-------------------*/

$colorLightGrey: #E9E9E9;
$colorDarkGrey: #0c0c0c;
$colorFields: aqua;
$colorFirst: rgb(255, 242, 0);
$colorThird: rgb(65, 65, 65);
$colorSecond: rgb(255, 206, 213);

$mobileMaxWidth: 768px;
$padMinWidth: 769px;
$padMaxWidth: 991px;

/*-------------------
----- KEYFRAMES -----
-------------------*/

@keyframes float {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-20px);
	}
	100% {
		transform: translatey(0px);
	}
}

@keyframes heightRetract {
    0% { 
        padding-top: 0%;
        padding-bottom: 0%;
    }
    25% {
        padding-top: 0%;
        padding-bottom: 0%;
       
    }
    100% {
        padding-top: 16px;
        padding-bottom: 16px;
       
    }
}

@keyframes heightRetractInvert {
 
    0% {
        padding-top: 16px;
        padding-bottom: 16px;
       
    }
    100% {
        padding-top: 0;
        padding-bottom: 0;
       
    }
}

@keyframes navOpen {
    0% { 
        opacity: 0;  
    }
    100% {
        opacity: 1;
    }
}

@keyframes navOpenInvert {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes showHamburger {
    0% { 
        opacity: 0;
    }
   
    100% {
        opacity: 1;
    }
}
@keyframes hideHamburger {
    0% { 
        opacity: 1;    
    }
   
    100% {
        opacity: 0;  
    }
}

@keyframes openHeader {
    0% {  
        height: 30px;
    }
    
    100% { 
        height: 98px;
    }
}

@keyframes closeVerticalHeader {
    0% {  
        width: 40%;
    }
    
    100% { 
        width: 6%;
    }
}

@keyframes openVerticalHeader {
    0% {  
        width: 6%;
    }
    
    100% { 
        width: 40%;
    }
}

@keyframes closeHeader {
    0% {  
        height: 98px;
    }
    
    100% { 
        height: 30px;
    }
}


@keyframes disappear {
    0% {  
        opacity: 1;
    }

    100% { 
        opacity: 0;
        display: none;
    }
}

@keyframes closeCollapse {

    0% {  
        transform: rotate(180deg);
    }

    100% { 
        transform: rotate(0deg);
    }
    
}

@keyframes openCollapse {
    0% {  
        transform: rotate(0deg);
    }

    100% { 
        transform: rotate(180deg);
    }
}

@keyframes openCollapseChildren {
    0% {  
        height: 0%;
        opacity: 0;
        // padding-bottom: 0px;
        display: none;
    }
    50% { 
        height: 100%;
        opacity: 0;
        // padding-bottom: 22px;
    }

    100% { 
        height: 100%;
        opacity: 1;
        // padding-bottom: 22px;
    }
}

@keyframes closeCollapseChildren {
    0% {  
        height: 100%;
        opacity: 1;
        // padding-bottom: 22px;
    }
    50% {  
        height: 100%;
        opacity: 0;
        // padding-bottom: 22px;
    }

    100% { 
        height: 0%;
        opacity: 0;
        // padding-bottom: 0px;
        display: none;
    }
}

@keyframes appearOpacity {
    0% {  
        opacity: 0;
    }
    100% {  
        opacity: 1;
    }

}


